@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@300;400;600;700&family=Cutive&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box;background-color:rgba(0,0,0,0.95)}*,*:before,*:after{box-sizing:inherit;font-family:'Assistant', sans-serif}.cardContainer{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:2rem;margin:3% 5%;justify-items:center;min-height:100vh}.navBar .navItem{display:flex;justify-content:center;align-items:center}.navBar .navItem a{text-decoration:none;color:#fff;padding:0rem 2rem}.navBar nav{display:flex;justify-content:space-between;box-shadow:2px 2px 20px rgba(0,0,0,0.5);border-bottom:solid #fff 1px;align-items:center}.navBar nav ul{display:flex;justify-content:flex-end;list-style-type:none}.navBar nav ul a{text-decoration:none}.navBar nav ul li{color:#fff;padding:0rem 1rem}.spinner{text-align:center;color:#fff;font-size:2.5rem;margin:2rem}.spinner p{font-size:1.5rem}.heroBanner{height:175px;position:relative}.heroBanner .overlay{width:100%;height:100%;background:rgba(0,0,0,0.6)}.heroBanner .heading{position:absolute;text-align:center;top:2rem;width:100%}.heroBanner h1{color:white;font-family:'Cutive', serif}.heroBanner img{position:absolute;top:0;height:100%;width:100%;object-fit:cover;z-index:-10}.characterCard{width:100%;border-radius:2rem;box-shadow:2px 2px 10px rgba(0,0,0,0.5);height:350px;position:relative;transition:200ms ease-in-out}.characterCard:hover{-webkit-transform:scale(1.01);transform:scale(1.01);box-shadow:2px 2px 20px rgba(0,0,0,0.5)}.characterCard img{height:350px;width:100%;object-fit:cover;border-radius:2rem}.characterCard .characterName{position:absolute;bottom:0px;background:rgba(0,0,0,0.7);padding:0rem 1rem;width:100%;border-radius:0rem 0rem 2rem 2rem;text-align:center;color:white}.characterCard .characterName h2{font-size:1.2rem;font-weight:400}.characterCard .comicPill{position:absolute;top:1rem;right:1rem;background:rgba(0,0,0,0.8);color:#fff;border-radius:1rem;padding:0.25rem 2rem}.characterCard .removeFavorite{position:absolute;top:1rem;left:1rem;color:white;background:rgba(0,0,0,0.8);border-radius:5px;padding:0.5rem 1rem}.pagination ul{display:flex;justify-content:center;color:white}.pagination ul li{list-style-type:none;padding:1rem;cursor:pointer}.singleHeroBanner{display:grid;position:relative;height:300px;background:black;padding:1rem 2rem;grid-template-columns:1fr 4fr;grid-gap:2rem}.singleHeroBanner .singleImage{align-self:center}.singleHeroBanner img{height:250px;width:250px;object-fit:cover}.singleHeroBanner h2{color:#fff;padding:0;margin:0;font-size:4rem;font-weight:300}.singleHeroBanner .stats{color:#fff}.singleHeroBanner p{font-weight:300;font-size:1rem}.singleHeroBanner .statsBold{font-weight:700;padding-right:0.5rem}.singleHeroBanner button{position:absolute;top:1rem;right:2rem;display:flex;justify-content:center;align-items:center}.singleHeroBanner .icon{padding-right:0.5rem;font-size:1.5rem;color:goldenrod}.modal{position:fixed;width:50vw;max-width:500px;min-width:300px;background:#fff;color:#444;min-height:200px;left:50%;top:20%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border-radius:2rem;display:flex;justify-content:center;align-items:center}.modal p{font-size:1.2rem}.message{text-align:center;color:#fff;font-size:2rem;margin:2rem}.popularPublishers{align-items:center;display:grid;grid-template-columns:repeat(3, 1fr);justify-items:center;height:60px;background:#fff}.popularPublishers .publisher{border-right:solid black 5px;-webkit-transform:skew(-45deg);transform:skew(-45deg);height:100%;width:100%;display:flex;align-items:center;justify-content:center;cursor:pointer}.popularPublishers .last{border-right:none}.popularPublishers p{-webkit-transform:skew(45deg);transform:skew(45deg);margin:0;padding:0}.statsBar{align-items:center;display:grid;grid-template-columns:repeat(3, 1fr);justify-items:center;height:60px;background:#fff;border:solid 2px #444;border-right:none;border-left:none}.statsBar .stats{border-right:solid black 2px;height:100%;width:100%;display:flex;align-items:center;justify-content:center;cursor:pointer}.statsBar .last{border-right:none}.statsBar p{margin:0;padding:0}.statContent{height:500px;color:#fff;background:black;padding:1rem 4rem}.statContent .grid{display:grid;grid-template-columns:repeat(3, 1fr)}.statContent .appearanceGrid{display:grid;grid-template-columns:1fr 2fr;align-items:center}.statContent .appearanceGrid img{width:100%;height:250px;object-fit:cover;border-radius:2rem}.statContent h3{font-size:1.5rem}.statContent .stat{font-weight:700;padding-right:0.5rem}.statContent p{font-weight:300}

